@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Mono&display=swap);
*{margin:0;padding:0;box-sizing:border-box}html{background-color:#000000}.App{max-width:1280px;min-height:100vh;line-height:2em;margin:0 auto}.App .user_input{width:100%;height:49vh;border:1px solid #ffffff}.App .user_input .input_header{background-color:whitesmoke;height:2em;font-family:'Ubuntu Mono', Tahoma, sans-serif;padding-left:1em}.App .user_input .editor_wrapper{width:100%;height:calc(100% - 2em)}.App .user_input .editor_wrapper #editor{width:100%;height:100%;padding:1em;resize:none;outline:none;border:none;background-color:#000000;color:lightgray;font-size:1.25em}.App .previewer{width:100%;max-height:calc(50vh - 1em);overflow:auto;border:1px solid #ffffff;background-color:#300a24}.App .previewer .previewer_header{background-color:#696969;color:#ffffff;height:2em;padding-left:1em;font-family:'Ubuntu Mono', Tahoma, sans-serif}.App .previewer #preview{padding:1em;overflow:hidden;height:100%;font-family:'Ubuntu Mono', Tahoma, sans-serif;color:whitesmoke}.App .previewer #preview li{margin-left:2em}.App .previewer #preview a{color:lightskyblue}.App .code{width:100%;height:1em;text-align:center}.App .code a{text-decoration:none;font-family:"Ubuntu Mono", Tahoma, sans-serif;font-size:.75em;color:#00FF41}@media (min-width: 960px){.App{display:flex;flex-direction:column;justify-content:center}.App main{display:flex;flex-direction:row}.App main .user_input{width:50%;height:70vh}.App main .previewer{width:50%;min-height:70vh}}

